

import { Vue, Options } from 'vue-class-component';

const API_URL = '/messages';

@Options({
    data() {
        return {
            error: '',
            newMessage: {
                name: '',
                email: '',
                phone: '',
                message: '',
                okToText: false,
                okToCall: false,
            },
        };
    },
    watch: {
        error (newValue): void {
            console.log(newValue);
        }
    }
})
export default class ContactUs extends Vue {
    // Any error will be set to this variable.
    error!: string;

    // For storing the posted API data.
    newMessage!: Record<string, unknown>;

    public onCompositionUpdate(event: CompositionEvent, property: string): void {
        this.newMessage[property] = event.data;
    }

    public postNewMessage(): void {

        const postObject = {
            method: 'POST',
            body: JSON.stringify(this.newMessage),
            headers: {
                'content-type': 'application/json'
            }
        };

        fetch(API_URL, postObject);
    }
}

